.image-viewer-popup-layer {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  .close-btn {
    top: 10vw;
    right: 10vw;
    width: 4.8vw;
    height: 5vw;
    background: url(/_ui/img/m/icn/icn-close.png) no-repeat;
    background-size: 100%;
    font-size: 0;
    text-indent: -9999px;
    position: absolute;
    display: block;
  }

  .image-wrapper {
    img {
      width: 100%;
    }
  }
}
