@-webkit-keyframes fadeInDownShort {
  from {
    opacity: 0;
    transform: translate3d(0, -5%, 0);
  }

  to {
    opacity: 1;
    transform: none;
  }
}

@keyframes fadeInDownShort {
  from {
    opacity: 0;
    transform: translate3d(0, -5%, 0);
  }

  to {
    opacity: 1;
    transform: none;
  }
}

.fadeInDownShort {
  animation-duration: .3s;
  -webkit-animation-name: fadeInDownShort;
  animation-name: fadeInDownShort;
}
