//scr-navi
.scr-navi {
  position: relative;
  padding: 0 4.8vw;
  overflow: hidden;
  //width: 90.4vw;
  //margin: 0 auto;
  .swiper-slide {
    width: auto;
    padding: 0 2.66vw;
    &:first-child {
      padding-left: 0;
    }
    &:last-child {
      padding-right: 0;
    }
    a {
      color: #aaa;
      &.on {
        color: #1f1f1f;
      }
      &.main {
        color: #1f1f1f;
        font-weight: bold;
      }
    }
  }
  .swiper-slide.swiper-slide-active {
    a {
      //color: #1f1f1f;
    }
  }
  .icn-navi {
    content: '';
    display: block;
    width: 20.4vw;
    height: 15.333vw;
    background: url('/_ui/img/m/icn/icn-arrow.png') no-repeat;
    background-size: 100%;
    position: absolute;
    top: 0;
    z-index: $icn-navi-zindex;
  }
  .icn-navi.left {
    left: 0;
    -webkit-transform: rotate(-180deg);
    -moz-transform: rotate(-180deg);
    -o-transform: rotate(-180deg);
    -ms-transform: rotate(-180deg);
    transform: rotate(-180deg);
  }
  .icn-navi.right {
    left: inherit;
    right: 0;
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  a {
    font-size: 3.2vw;
    color: #1f1f1f;
    font-weight: 500;
    display: block;
    width: 100%;
    text-align: center;
    height: 15.333vw;
    line-height: 15.333vw;
  }
  .sub-line {
    border-bottom: .8vw #000 solid;
    position: relative;
    margin-bottom: -.8vw;
    z-index: 2;
  }
}
