      font-size: 3.6vw;
      letter-spacing: -.133vw;
      text-overflow: ellipsis;
      white-space: nowrap;
      word-wrap: normal;
      overflow: hidden;
      width: 43.466vw;
    }
    .btm {
      height: 10vw;
      margin-top: 4.133vw;
      position: relative;
    }
