.cs-depth3 {
  padding: 0 4.8vw;
  ul {
    overflow: hidden;
    margin: 4.8vw 0 9.2vw;
    li {
      float: left;
      width: 33.333%;
      height: 10.666vw;
      box-sizing: border-box;
      border-top: 1px #f1f1f1 solid;
      border-right: 1px #f1f1f1 solid;
      border-bottom: 1px #f1f1f1 solid;
      &:first-child {
        border-left: 1px #f1f1f1 solid;
      }
      &:nth-child(4) {
        border-top: 0;
        border-left: 1px #f1f1f1 solid;
      }
      &:nth-child(5) {
        border-top: 0;
      }
      &:nth-child(6) {
        border-top: 0;
      }
      a {
        line-height: 10.666vw;
        display: block;
        width: 100%;
        height: 100%;
        font-size: 3.466vw;
        color: #aaa;
        text-align: center;
      }
      &.bd0 {
        border-top: 0;
      }
      &.on {
        a {
          background: #1f1f1f;
          color: #fff;
        }
      }
    }
  }
}
.cs-depth3.col2 {
  li {
    width: 50%;
    &:nth-child(3) {
      border-top: 0;
      border-left: 1px #f1f1f1 solid;
    }
    &:nth-child(4) {
      border-left: 0;
    }
  }
}
.cs-depth3.type02 ul li {
