// select
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  display: block;
  width: 100%;
  border: 0;
  outline: 0;
}
select::-ms-expand {
  display: none;
}
// checkbox
.ch-area {
  overflow: hidden;
  .check {
    display: block;
    float: left;
    width: 6.133vw;
    margin-right: 2.4vw;
    input {
      width: 0;
      height: 0;
      font-size: 0;
      line-height: 0;
      overflow: hidden;
      visibility: hidden;
      position: absolute;
    }
    input + label {
      display: block;
      width: 6.133vw;
      height: 6.133vw;
      background: url('/_ui/img/m/icn/icn-check04.png') no-repeat;
      background-size: 100%;
    }
    input:checked + label {
      background: url('/_ui/img/m/icn/icn-check04-on.png') no-repeat;
      background-size: 100%;
      opacity: 1;
    }
    input:disabled + label {
      background: url('/_ui/img/m/icn/icn-check05.png') no-repeat;
      background-size: 100%;
    }
  }
  .check + label {
    font-size: 3.733vw;
    height: 6.133vw;
    line-height: 6.133vw;
    font-weight: 700;
    color: #1f1f1f;
    display: block;
    float: left;
  }
  .check.small {
    margin-top: .8vw;
    input + label {
      width: 4.533vw;
      height: 4.533vw;
      background: url('/_ui/img/m/icn/icn-check07.png') no-repeat;
      background-size: 100%;
    }
    input:checked + label {
      background: url('/_ui/img/m/icn/icn-check07-on.png') no-repeat;
      background-size: 100%;
    }
  }
  .check.small + label {
    font-weight: 400;
    color: #898989;
  }
  .check.small + label.on {
    color: #1f1f1f;
  }
}
// layer pop
.backgroundpopup {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: $layer-pop-bg;
  background: #000;
}
.layerpop {
  display: none;
  position: absolute;
  left: 50%;
  z-index: $layer-pop;
  background: #fff;
  -webkit-transform: translateX(-50%);
  -moz-transform: translateX(-50%);
  -o-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
}
// slide
.swiper-wrapper {
  max-width: 750px;
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-transition-property: -webkit-transform;
  -moz-transition-property: -moz-transform;
  -o-transition-property: -o-transform;
  -ms-transition-property: -ms-transform;
  transition-property: transform;
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
  .swiper-slide {
    background: #fff;
    webkit-flex-shrink: 0;
    -ms-flex: 0 0 auto;
    flex-shrink:  0;
    height:  100%;
    position:  relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
  }
  .swiper-slide>a {
    display: block;
  }

  img {
    width: 100%;
  }
}
//slide scroll pagination
.scr-prod-pagination {
  width: 100%;
  height: .8vw;
  background: #fff;
  position: relative;
  text-align: center;
