.layer-share {
  display: none;
  position: fixed;
  top: 0;
  width: 100vw;
  height: 100vh;
  z-index: 100;
  #overlay_t {
    display: block;
    opacity: .7;
    z-index: 0;
  }
  .pop-share {
    position: relative;
    top: 50%;
    left: 15%;
    width: 70%;
    height: 64vw;
    margin-top: -32vw;
    background: #fff;
    .sns-wrap {
      overflow: hidden;
      width: 59.333vw;
      margin: 0 auto;
      text-align: center;
      p {
        padding: 10vw 0;
        font-weight: bold;
        font-size: 3.6vw;
      }
      & > a {
        display: block;
        width: 12.8vw;
        float: left;
        margin: 0 3.466vw;
        font-size: 3.1vw;
        color: #1f1f1f;
        img {
          margin-bottom: 1.5vw;
          width: 100%;
        }
      }
    }
    button {
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 13vw;
      border-top: 2px solid #f3f3f3;
      font-weight: bold;
      font-size: 3.6vw;
    }
  }
}

@media (min-width: 750px) {
  //sns 
  .layer-share {
    .pop-share {
      height: 480px;
      margin-top: -240px;
      .sns-wrap {
        width: 445px;
        padding: 0 40px;
        p {
          padding: 75px 0;
          font-size: 27px;
        }
        & > a {
          width: 96px;
          margin: 0 26px;
          font-size: 23px;
          img {
            margin-bottom: 11px;
          }
        }
      }
      button {
        height: 98px;
        font-size: 27px;
      }
    }
  }
}
